.main {
    display: flex;
    /* margin-top: 14%; */
    height: 100vh;
    flex-direction: column;
    justify-content: center;
    /* justify-content: space-between; */
    align-items: center;
    gap: 20%;
}

.main .title {
    text-align: center;
    font-size: 160%;
    font-weight: 800;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* background-color: #A6DAF4; */
    border-style: dashed;
    border-width: 4px;
    border-radius: 30px;
    border-color: white;
    color: rgba(255, 129, 74);
    width: 80vw;
    height: 8vh;
    padding: auto;
}

.main .team {
    /* margin-bottom: 50px; */
    width: 100%;
    height: 4%;
    /* background-color: white; */
    display: flex;
    font-size: 12px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* font-size: 130%; */

    color: rgba(255, 129, 74);
}

.main .button .image {
    display: flex;
    background-image: url(../public/free-icon-coupons-15268294.png);
    width: 60vw;
    height: 30vh;
    background-repeat: no-repeat;
    background-size: 100%;

}

.main .button {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10px;
}

.custom-btn {
    margin-top: 8%;
    width: 100%;
    height: 40px;
    color: #fff;
    border-radius: 5px;
    padding: 10px 25px;
    font-family: 'Lato', sans-serif;
    font-weight: 500;
    background: transparent;
    cursor: pointer;
    transition: all 0.3s ease;
    position: relative;
    display: inline-block;
    box-shadow: inset 2px 2px 2px 0px rgba(255, 255, 255, .5),
        7px 7px 20px 0px rgba(0, 0, 0, .1),
        4px 4px 5px 0px rgba(0, 0, 0, .1);
    outline: none;
}

/* 10 */
.btn-10 {
    background: rgb(206, 203, 255);
    background: linear-gradient(0deg, rgb(176, 173, 223) 0%, rgb(88, 131, 225) 100%);
    color: #fff;
    border: none;
    transition: all 0.3s ease;
    overflow: hidden;
}

.btn-10:after {
    position: absolute;
    content: " ";
    top: 0;
    left: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    transition: all 0.3s ease;
    -webkit-transform: scale(.1);
    transform: scale(.1);
}

.btn-10:hover {
    color: #fff;
    border: none;
    background: transparent;
}

.btn-10:hover:after {
    background: rgb(0, 3, 255);
    background: linear-gradient(0deg, rgb(176, 173, 223) 0%, rgb(88, 131, 225) 100%);
    -webkit-transform: scale(1);
    transform: scale(1);
}